export const Button = ({
    children,
    type = 'click',
    className,
    handlerClick,
    label,
  }) => {
    //  ,       type="value"-string
    //    ,   <Button className={your style }></Button>
  
    return (
      <button
        type={type}
        className={className}
        onClick={handlerClick}
        aria-label={label}
      >
        {children}
      </button>
    );
  };